.listContent .description {
  line-height: 22px;
  max-width: 720px; }

.listContent .extra {
  color: fade(#000, 45%);
  margin-top: 16px;
  line-height: 22px; }
  .listContent .extra > :global(.ant-avatar) {
    vertical-align: top;
    margin-right: 8px;
    width: 20px;
    height: 20px;
    position: relative;
    top: 1px; }
  .listContent .extra > em {
    color: fade(#000, 25%);
    font-style: normal;
    margin-left: 16px; }

a.listItemMetaTitle {
  color: fade(#000, 85%); }

.listItemExtra {
  width: 272px;
  height: 1px; }

.selfTrigger {
  margin-left: 12px; }

@media screen and (max-width: 480px) {
  .selfTrigger {
    display: block;
    margin-left: 0; }
  .listContent .extra > em {
    display: block;
    margin-left: 0;
    margin-top: 8px; } }

@media screen and (max-width: 768px) {
  .selfTrigger {
    display: block;
    margin-left: 0; } }

@media screen and (max-width: 992px) {
  .listItemExtra {
    width: 0;
    height: 1px; } }



