      padding-left: 34px; }
  .main :global .ant-tabs .ant-tabs-bar {
    border-bottom: 0;
    margin-bottom: 24px;
    text-align: center; }
  .main :global .ant-form-item {
    margin-bottom: 24px; }
  .main .prefixIcon {
    font-size: 14px;
    color: fade(#000, 25%); }
  .main .getCaptcha {
    display: block;
    width: 100%; }
  .main .submit {
    width: 100%;
    margin-top: 24px; }



